






































































































































































































import Vue from 'vue';
import {mapMutations, mapState} from 'vuex';
import Oa from "@/models/Oa";
import UnitPlan from "@/models/UnitPlan";
import UnitPlanOa from "@/models/UnitPlanOa";

function requestDefaultValues(index: number) {
    return {
        school_id: null,
        name: 'Unidad ' + index,
        subject: null,
        schoolLevel: null,
        level: null,
        course: null,
        year: new Date().getFullYear(),
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10),
        oas: [],
    };
}

export default Vue.extend({
    name: 'RomaUnitModalForm',
    created() {
        // si va a editar
        if (this.unit) {
            this.request = this.unit;
        }
    },
    props: {
        value: Boolean,
        unit: Object,
        school: {
            type: Object,
            required: true,
        },
        maxWidth: {
            type: String,
            default: '800',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            request: requestDefaultValues((this.school.unitplans.length + 1)),
            valid: false,
            loading: false,
            showing: this.value,
            startDateMenu: false,
            endDateMenu: false,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
        };
    },
    computed: {
      ...mapState([
          'roma',
      ]),
    },
    methods: {
        show(value) {
            this.$emit('input', value);
            this.request = requestDefaultValues((this.school.unitplans.length + 1));
        },
        setUnit() {
            this.request.school_id = this.school.id;
            // this.request.oas = oas;
            UnitPlan.insertOrUpdate({data: this.request}).then(data => {
                let oas = Oa.all(); // Obtengo todos los oas con sus ies

                oas = oas.map(oa => {
                    return {
                        unitplan_id: data.unit_plans[0].id,
                        oa_id: oa.id
                    }
                });

                UnitPlanOa.insert({data: oas});
            });
            this.show(false);
            this.request = requestDefaultValues((this.school.unitplans.length + 1));
        },
    },
});
