















































































































































































import Vue from 'vue';
import { mask } from 'vue-the-mask';

export default Vue.extend({
    name: 'RomaOrganizer',
    props: {
        school: {
            type: Object,
            required: true,
        },
        expanded: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            mask: '###',
            expand: this.expanded,
            menuStartDate: false,
            menuEndDate: false,
            startDate: new Date().toISOString().substr(0, 10),
            endDate: new Date().toISOString().substr(0, 10),
            selectedPlan: '',
            selectedUnit: '',
            selectedSubject: '',
            selectedCurse: '',
            selectedLevel: '',
        };
    },
    computed: {
        // TODO: Cambiar todo esto a querys del ORM
        isExpanded: {
          get() { return (this.expanded && this.school.unitplans.length > 0) ? 0 : -1; },
          set(value) { this.expand = value; },
        },
        planeFilters() {
            return this.school.unitplans.map((unit) => {
                return new Date(unit.startDate).getFullYear();
            });
        },
        subjectFilters() {
          return this.school.unitplans.map((unit) => unit.subject);
        },
        courseFilters() {
            return this.school.unitplans.map((unit) => unit.course);
        },
        levelFilters() {
            return this.school.unitplans.map((unit) => unit.level);
        },
       /* minDate() {
          const years = this.school.unitplans.map((unit) => new Date(unit.startDate).getFullYear());
          return years.sort()[0] + '01-01';
        },
        maxDate() {
            const years = this.school.unitplans.map((unit) => new Date(unit.endDate).getFullYear());
            return years.sort()[years.length - 1] + '12-31';
        },*/
    },
    directives: {
        mask
    }
});
